import './index.scss';
import { useTranslation } from 'react-i18next';
import profi1 from '../../../assets/images/profi-1.png';
import profi2 from '../../../assets/images/profi-2.png';
import profi3 from '../../../assets/images/profi-3.png';
import profi4 from '../../../assets/images/profi-4.png';

function AboutTeam() {
  const { t } = useTranslation();

  return (
    <div className="about__team">
      <div className="container">
        <div className="about__team__title">
          <span className="text-sm pn-light">
            { t('aboutUs.meetOurTeam') }
          </span>
          <h2 className="pn-bold">
            { t('aboutUs.teamProfs') }
          </h2>
        </div>
        <div className="about__team__cards d-flex">
          <div className="about__team__card">
            <img src={profi1} alt="" />
          </div>
          <div className="about__team__card">
            <img src={profi2} alt="" />
          </div>
          <div className="about__team__card">
            <img src={profi3} alt="" />
          </div>
          <div className="about__team__card">
            <img src={profi4} alt="" />
          </div>
        </div>
      </div>
    </div>
  )
}

export default AboutTeam;