import './index.scss';
import { useTranslation } from 'react-i18next';
import BaseSectionTitle from '../../BaseSectionTitle';
import AboutSmall from '../../AboutSmall';
import aboutImg from '../../../assets/images/about.png';
import smallIcon1 from '../../../assets/images/award.svg';
import smallIcon2 from '../../../assets/images/sliders.svg';
import smallIcon3 from '../../../assets/images/user-check.svg';
import smallIcon4 from '../../../assets/images/zap.svg';

function MainAbout() {
  const { t } = useTranslation();

  return (
    <div className="about__company">
      <div className="container">
        <div className="about__company__inner d-flex">
          <div className="about__company-left">
            <img src={aboutImg} alt="" />
          </div>
          <div className="about__company-right d-flex">
            <BaseSectionTitle titleSmall="main.about" titleBig="pga" />
            <p className="py-1 text-md">
              { t('aboutUs.aboutT1') }
            </p>
            <p className="py-1 text-md">
              { t('aboutUs.aboutT2') }
            </p>
            <p className="py-1 text-md">
              { t('aboutUs.aboutT3') }
            </p>
            <p className="py-1 text-md">
              { t('aboutUs.aboutT4') }
            </p>
            <div className="about__company__cards d-flex">
              <div><AboutSmall icon={smallIcon1} text="aboutUs.aboutCardT1" /></div>
              <div><AboutSmall icon={smallIcon2} text="aboutUs.aboutCardT2" /></div>
              <div><AboutSmall icon={smallIcon3} text="aboutUs.aboutCardT3" /></div>
              <div><AboutSmall icon={smallIcon4} text="aboutUs.aboutCardT4" /></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default MainAbout;